import request from '@/utils/request'

//  获取发育量模板列表
export function getGrowList(params) {
  return request({
    url: '/growth_info/list',
    method: 'get',
    params
  })
}
//  创建发育量模板
export function createGrow(data) {
  return request({
    url: '/growth_info',
    method: 'post',
    data
  })
}
//  更新发育量模板
export function updateGrow(id, data) {
  return request({
    url: '/growth_info/' + id,
    method: 'put',
    data
  })
}
//  删除发育量模板
export function deleteGrow(data) {
  return request({
    url: '/growth_info',
    method: 'delete',
    data
  })
}
//  发育量模板详情
export function getGrowDetail(id) {
  return request({
    url: '/growth_info/' + id,
    method: 'get'
  })
}
//  发育量模板状态更改
export function getGrowStatus(id) {
  return request({
    url: '/growth_info/' + id + '/status',
    method: 'put'
  })
}
//  获取发育量列表
export function getGrowthList(params) {
  return request({
    url: '/family_member/in_server_list',
    method: 'get',
    params
  })
}
//  获取发育量列表
export function getGrowthDetail(params) {
  return request({
    url: 'baby_growth_info/growth',
    method: 'get',
    params
  })
}
//  获取发育详情
export function getGrowthStatus(id) {
  return request({
    url: 'baby_growth_info/detail/' + id,
    method: 'get'
  })
}
//  发送发育量表
export function sendGrowth(id) {
  return request({
    url: 'baby_growth_info/send/' + id,
    method: 'get'
  })
}
//  发送发育量表
export function createFeedback(data) {
  return request({
    url: 'baby_growth_info/content',
    method: 'post',
    data
  })
}
