<template>
  <div class="status-detail">
    <el-button
      class="float-right"
      v-if="showBtn"
      type="primary"
      plain
      size="mini"
      @click="returnList"
    >
      返回
    </el-button>
    <div class="user-info">
      <div class="info-content">
        <el-avatar :src="user.avatar" />
        <p>
          <span class="item-title" style="width: 80px;">用户名称：</span
          ><span class="item-text">{{ user.nickname }}</span>
        </p>
        <p>
          <span class="item-title">宝宝：</span
          ><span class="item-text">{{ member.name }}</span>
        </p>
        <p>
          <span class="item-title" style="width: 80px;">宝宝月龄：</span
          ><span class="item-text">{{ member.birthday }}</span>
        </p>
      </div>
    </div>
    <div class="card-question">
      <div class="m-b-20">
        <span class="item-title">模板名称：</span
        ><span class="item-text">{{ name }}</span>
      </div>
      <div class="m-b-20">
        <span class="item-title">测试月龄：</span
        ><span class="item-text">{{ monthAge }}</span>
      </div>
      <el-card
        v-for="(item, index) of questions"
        :key="index"
        shadow="never"
        class="m-b-20"
      >
        <div class="question m-b-20">
          <p class="item-title question-title">问题{{ index + 1 }}：</p>
          <p class="question-text" style="line-height: 16px;">
            {{ item.question }}
          </p>
        </div>
        <div class="answer">
          <span class="item-title answer-title">答案：</span>
          <p>
            <span
              v-for="(answer, i) of item.answer"
              :key="answer.key"
              class="item-text"
              :class="
                $route.query.status !== '未发送' && answers[index] === i
                  ? 'answer-active'
                  : ''
              "
              >{{ answer.value }}</span
            >
          </p>
        </div>
      </el-card>
      <div v-if="submitTime" class="time">
        {{ testTime }}
      </div>
      <el-form
        v-if="
          (status === 'save' || status === 'tested') &&
            $store.state.user.bus_role === 'doctor'
        "
        ref="createSuggestForm"
        :model="suggestForm"
        :rules="rules"
        label-width="100px"
        label-position="left"
      >
        <el-form-item label="医生建议：" prop="suggest">
          <el-input
            v-model="suggestForm.suggest"
            type="textarea"
            placeholder="请输入您的建议"
            maxlength="200"
            show-word-limit
            resize="none"
            :rows="4"
          />
        </el-form-item>
        <div
          v-if="status === 'suggested' || status === 'save'"
          class="time"
          style="margin-bottom: 20px;"
        >
          {{ updateTime }}
        </div>
        <el-form-item v-if="status !== 'suggested'" class="form-btn">
          <el-button @click="submitForm('createSuggestForm', 'save')"
            >保存</el-button
          >
          <el-button
            type="primary"
            @click="submitForm('createSuggestForm', 'suggested')"
            >发送</el-button
          >
        </el-form-item>
      </el-form>
      <div v-if="status === 'suggested'" class="m-b-20">
        <span class="item-title">医生建议：</span
        ><span class="item-text">{{ suggestForm.suggest }}</span>
      </div>
      <div
        v-if="status === 'suggested'"
        class="time"
        style="margin-bottom: 20px;"
      >
        {{ updateTime }}
      </div>
    </div>
  </div>
</template>

<script>
import { getGrowthStatus, createFeedback } from '@/api/grow-table'
export default {
  props: {
    showBtn: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      suggestForm: {
        suggest: ''
      },
      rules: {
        suggest: [{ required: true, message: '内容不能为空' }]
      },
      questions: [],
      member: {},
      user: {},
      monthAge: '',
      name: '',
      status: '',
      answers: [],
      testTime: '',
      updateTime: ''
    }
  },
  computed: {
    submitTime() {
      if (
        this.status === 'un_send' ||
        this.status === 'pre_send' ||
        this.status === 'send'
      )
        return false
      return true
    }
  },
  created() {
    this.getGrowthStatus()
  },
  methods: {
    returnList() {
      this.$emit('return')
    },
    async getGrowthStatus() {
      const { data, code } = await getGrowthStatus(
        this.$route.params.id || this.$store.state.chat.growthId
      )
      if (code === 20000 && data.result) {
        this.member = data.growth.member
        this.user = data.growth.user
        this.questions =
          data.growth.growth_info && data.growth.growth_info.questions
        this.monthAge = data.growth.month_age
        this.name = data.growth.name
        this.suggestForm.suggest = data.growth.suggest_content || ''
        this.status = data.growth.status
        this.answers = data.growth.answers || []
        this.testTime = this.formatTime(data.growth.tested_at)
        this.updateTime = this.formatTime(data.growth.updated_at)
      }
    },
    submitForm(formName, status) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          const { data, code } = await createFeedback({
            id: this.$route.params.id,
            content: this.suggestForm.suggest,
            status
          })
          if (code === 20000) {
            if (data.result) {
              this.$message.success(
                (status === 'save' ? '保存' : '发送') + '成功'
              )
              this.$router.go(-1)
            } else this.$message.error(data.message)
          }
        } else {
          return false
        }
      })
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (
        vm.$route.query.status === '未发送' ||
        vm.$route.query.status === '待发送' ||
        vm.$route.query.status === '待测试'
      )
        vm.$route.meta.title = '未发送'
      if (vm.$route.query.status === '待反馈') vm.$route.meta.title = '待反馈'
      if (vm.$route.query.status === '反馈已保存')
        vm.$route.meta.title = '填写反馈'
      if (vm.$route.query.status === '已完成') vm.$route.meta.title = '已完成'
    })
  }
}
</script>

<style scoped lang="scss">
.status-detail {
  overflow: auto;
  padding: 8px;
  height: 100%;
  border: 1px solid #e8eaec;
  border-radius: 4px;
  .user-info {
    display: flex;
    line-height: 40px;
    margin-bottom: 20px;
    .el-avatar {
      margin-right: 8px;
      width: 40px;
      height: 40px;
    }
  }
  .item-title {
    font-weight: 600;
    flex-shrink: 0;
  }
  .item-text {
    margin-right: 20px;
    flex-shrink: 0;
  }
  .info-content {
    display: flex;
    flex-wrap: wrap;
  }
  .card-question {
    max-width: 1000px;
  }
  .m-b-20 {
    margin-bottom: 20px;
  }
  .question {
    display: flex;
  }
  .question-title {
    width: 50px;
    flex-shrink: 0;
  }
  .answer {
    display: flex;
  }
  .answer-title {
    display: inline-block;
    width: 55px;
  }
  .time {
    font-size: 16px;
    color: #576277;
    margin-bottom: 50px;
  }
  .el-button {
    padding: 6px 20px;
    height: 32px;
  }
  .form-btn {
    text-align: center;
    /deep/.el-form-item__content {
      margin-left: 0 !important;
    }
  }
  .answer-active {
    color: #0085ff;
  }
}
</style>
